.container::before {
	content: '';
	position: absolute;
	width: 2000px;
	height: 2000px;
	border-radius: 50%;
	background: linear-gradient(-45deg, #4481eb, #04befe);
	top: -10%;
	right: 48%;
	transform: translateY(-50%);
	z-index: 6;
	transition: 1.8s ease-in-out;
}
.forms-container {
	position: absolute;
	width: 100%; height: 100%;
	top: 0;
	left: 0;
}
.signin-signup {
	position: absolute;
	top: 50%;
	left: 75%;
	transform: translate(-50%, -50%);
	width: 50%;
	display: grid;
	grid-template-columns: 1fr;
	z-index: 5;
	transition: 1s 0.7s ease-in-out;
}
form.sign-in-form,
form.sign-up-form {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 0 5rem;
	overflow: hidden;
	flex-direction: column;
	grid-column: 1 / 2;
	grid-row: 1 / 2;
	transition: 0.2s 0.7s ease-in-out;
}
form.sign-in-form {
	z-index: 2;
	opacity: 1;
}
form.sign-up-form {
	z-index: 1;
	opacity: 0;
}
.title {
	font-size: 2.2rem;
	color: #444;
	margin-bottom: 10px;
}
.input-field {
	max-width: 300px;
	width: 100%;
	height: 55px;
	background-color: #f0f0f0;
	margin: 10px 0;
	border-radius: 55px;
	display: grid;
	grid-template-columns: 15% 85%;
	padding: 0 .4rem;
	align-items: center
}
.input-field i {
	text-align: center;
	line-height: 55px;
	color: #acacac;
	font-size: 1.1rem;
	place-self: center;
}
.input-field input {
	background: none;
	outline: none;
	border: none;
	line-height: 1;
	font-weight: 600;	font-size: 1.1rem;
	color: #333;
}
.input-field input::placeholder {
	color: #aaa;
	font-weight: 500;
}
.btn {
	width: 9.5em;
	height: 3em;
	border: none;
	outline: none;
	border-radius: 3em;
	cursor: pointer;
	background-color: #5995fd;
	color: #fff;
	text-transform: uppercase;
	font-weight: 600;
	margin: 10px 0;
	transition: .5s;
}
.btn:hover {
	background-color: #4d84e2;
}
.social-text {
	padding: .7rem 0;
	font-size: 1rem;
}
.social-media {
	display: flex;
	justify-content: center;
}
.social-icon {
	height: 2.875em;
	width: 2.875em;
	border: 1px solid #333;
	margin: 0 .45rem;
	display: flex;
	justify-content: center;
	align-items: center;
	text-decoration: none;
	color: #333;
	font-size: 1.1rem;
	border-radius: 50%;
	transition: .3s;
}
.social-icon:hover {
	color: #4481eb;
	border-color: #4481eb;
}
.panels-container {
	position: absolute;
	width: 100%; height: 100%;
	top: 0; left: 0;
	display: grid;
	grid-template-columns: repeat(2, 1fr);
}
.panel {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: space-around;
	text-align: center;
	z-index: 7;
}
.left-panel {
	pointer-events: all;
	padding: 3rem 17% 2rem 12%;
}
.right-panel {
	pointer-events: none;
	padding: 3rem 12% 2rem 17%;
}
.panel .content {
	color: #fff;
	transition: .9s .6s ease-in-out;
}
.panel h3 {
	font-weight: 600;
	line-height: 1;
	font-size: 1.rem;
}
.panel p {
	font-size: 0.95rem;
	padding: 0.7rem 0;
}
.btn.transparent {
	margin: 0;
	background: none;
	border: 2px solid #fff;
	width: 130px;
	height: 41px;
	font-weight: 600;
	font-size: 0.8rem;
}
.image {
	width: 100%;
	transition: 1.1s .4s ease-in-out;
}
.right-panel .content, .right-panel .image {
	transform: translateX(800px);
}
/* ANIMATION */

.container.sign-up-mode::before {
	transform: translate(100%, -50%);
	right: 52%;
}
.container.sign-up-mode .left-panel .image,
.container.sign-up-mode .left-panel .content {
	transform: translateX(-800px);
}
.container.sign-up-mode .right-panel .image,
.container.sign-up-mode .right-panel .content {
	transform: translateX(0);
}
.container.sign-up-mode .left-panel {
	pointer-events: none;
}
.container.sign-up-mode .right-panel {
	pointer-events: all;
}

.container.sign-up-mode .signin-signup {
	left: 25%;
}

.container.sign-up-mode form.sign-in-form {
	z-index: 1;
	opacity: 0;
}
.container.sign-up-mode form.sign-up-form {
	z-index: 2;
	opacity: 1;
}

/* MEDIA QUERIES */

@media (max-width:870px) {
	.container {
		height: 100vh;
	}
	.container:before {
		width: 1500px;
		height: 1500px;
		left: 30%;
		bottom: 68%;
		transform: translateX(-50%);
		right: initial;
		top: initial;
		transition: 2s ease-in-out;
	}
	.signin-signup {
		width: 100%;
		left: 50%;
		top: 95%;
		transform: translate(-50%, -100%);
		transition: 1s 0.8s ease-in-out;
	}
	.panels-container {
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 2fr 1fr;
	}
	.panel {
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding: 2.5rem 8%;
	}
	.panel .content {
		padding-right: 15%;
	}
	.panel h3 {
		font-size: 1.2rem;
	}
	.panel p {
		font-size: 0.7rem;
		padding: 0.5rem 0;
	}
	.btn.transparent {
		width: 110px;
		height: 35px;
		font-size: .7rem;
	}
	.image {
		width: 200px;
		transition: 0.9s 0.8s ease-in-out;
	}
	.left-panel {
		grid-row: 1 / 2;
	}
	.right-panel {
		grid-row: 3 / 4;
	}
	.right-panel .content, .right-panel .image {
		transform: translateY(300px);
	}
	.container.sign-up-mode::before {
		transform: translate(-50%, 100%);
		bottom: 32%;
		right: initial;
	}
	.container.sign-up-mode .left-panel .image,
	.container.sign-up-mode .left-panel .content {
		transform: translateY(-300px);
	}
	.container.container.sign-up-mode .signin-signup {
		top: 5%;
		transform: translate(-50%, 0);
		left: 50%;
	}
}

@media (max-width: 600.99px) {
	.signin-signup {
		top: 85%;
	}
	.container.container.sign-up-mode .signin-signup {
		top: 15%;
	}
	form.sign-in-form,
	form.sign-up-form {
		padding: 0 1.5rem;
	}
	.title {
		font-size: 1.5rem;
	}
	.input-field  {
		height: 2.8em;
	}
	.input-field i  {
		line-height: 2.8em;
		font-size: 0.9rem;
	}
	.input-field input {
		font-size: 0.9rem;
	}
	.social-text {
		font-size: 0.9rem;
	}
	.social-icon {
		width: 2.3em;
		height: 2.3em;
	}
	.image {
		display: none;
	}

	.panel .content {
		padding: 0.5rem 1rem;
	}
	.container::before {
		bottom: 72%;
		left: 50%;
	}
	.container.sign-up-mode::before {
		bottom: 28%;
		left: 50%;
	}
}